import { makeStyles } from '@material-ui/core/styles';
import { get } from 'lodash';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  '@global': {
    '.c-collage-type-1 + [data-locator="Infobox"]': {
      marginTop: 0,
    },
  },
  secheading: {
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.paddingRight(150),
    },
  },
  root: {
    position: 'relative',
    ...theme.shape.componentSpacing,
    paddingBottom: theme.spacing(6),
    [theme.breakpoints.up('lg')]: {
      paddingBottom: theme.spacing(9),
    },

    '& + [data-locator="CollageType1"] > *:first-child': {
      marginTop: '0px',
    },
    '& .MuiButtonBase-root.btnWithBorder': {
      [theme.breakpoints.down('md')]: {
        paddingTop: '0',
      },
    },
    '& .card-description': {
      flexGrow: '1',
      justifyContent: 'space-between',
    },
    '& .slick-track': {
      display: 'flex',
      paddingBottom: theme.spacing(3),
      ...theme.mixins.marginLR('0', '0'),
      ...theme.mixins.paddingLeft(pxToRem(24)), //this is to compensate the margin on slick-list
    },
    '& .slick-list': {
      overflowX: 'auto',
      overflowY: 'unset',
      [theme.breakpoints.down('md')]: {
        ...theme.mixins.paddingLR('0 !important'),
      },
      [theme.breakpoints.up('lg')]: {
        overflowX: 'hidden',
        ...theme.mixins.marginLR(pxToRem(-24), pxToRem(-24)),
      },
    },
    '& .slick-slide': {
      display: 'flex',
      flex: '1',
      float: 'none',
      height: 'auto',
      cursor: 'pointer',
      backgroundColor: props => get(props, 'fields.backgroundColor'),
      transition: 'box-shadow .3s, background-color .3s',
      ...theme.mixins.marginLeft(pxToRem(0)),
      ...theme.mixins.marginRight(pxToRem(32)),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginLeft(pxToRem(0)),
        ...theme.mixins.marginRight(pxToRem(24)),
      },
      '&:hover': {
        boxShadow: theme.shadows[1],
        backgroundColor: theme.mixins.White(),
        '& .card-description': {
          borderBottomColor: 'transparent',
        },
        '& h3': {
          color: theme.palette.common.SeaPort,
        },
        '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
          transform: 'scale(1.1, 1.1)',
        },
      },
      '& > div, & > div > div': {
        width: '100%',
        height: '100%',
      },
    },
    '& .slick-slide img': {
      height: '100%',
      width: '100%',
    },
    '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
      transition: 'all 1s ease',
    },
    '& .slick-disabled': {
      opacity: theme.palette.action.disabledOpacity,
      pointerEvents: 'none',
    },
    '& .slick-prev, & .slick-next': {
      zIndex: '100',
      width: 'auto',
      height: 'auto',
      transition: 'opacity 0.5s ease-out',
      position: 'absolute',
    },
    '& .slick-prev > button, & .slick-next > button': {
      border: '1px solid #000',
      width: theme.spacing(8),
      height: theme.spacing(8),
      transition: 'all 0.3s ease-in-out',
      [theme.breakpoints.up('xl')]: {
        width: pxToRem(84),
        height: pxToRem(84),
      },
      '& .MuiTouchRipple-root': {
        display: 'none',
      },
      '& svg': {
        color: theme.mixins.Black(),
      },
      '&:hover': {
        backgroundColor: theme.mixins.Black(300),
      },
    },
    '& .slick-prev::before,& .slick-next::before': {
      content: "''",
    },
    '& .slick-prev': {
      top: '0',
      left: 'auto',
      ...theme.mixins.right('5.5%'),
      transform: 'none',
    },
    '& .slick-next': {
      ...theme.mixins.right('5.5%'),
      top: theme.spacing(16),
      [theme.breakpoints.up('xl')]: {
        top: theme.spacing(19.5),
      },
    },

    //Event Carousel Specific Overwites
    '&.isCollage': {
      background: theme.mixins.White(),
      paddingBottom: 0,
      marginTop: theme.spacing(-6),
      marginBottom: 0,
      [theme.breakpoints.up('lg')]: {
        marginTop: theme.spacing(-11),
        '& .MuiButton-root': {
          '&:hover': {
            '& .MuiButton-endIcon': {
              backgroundColor: `${theme.mixins.Black()} !important`,
              '& svg': {
                color: `${theme.mixins.White()}`,
              },
            },
          },
        },
      },
      '& .slick-slider': {
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.paddingLR('5.5%', '5.5%'),
        },
      },
      '& .slick-track': {
        [theme.breakpoints.up('lg')]: {
          margin: pxToRem(0),
        },
      },
      '& .slick-slide': {
        flex: '1',
        display: 'flex',
        float: 'none',
        height: 'auto',
        '& > div > div': {
          height: '100%',
        },
        '& .imageWrap': {
          maxHeight: pxToRem(229),
        },
        '& img': {
          [theme.breakpoints.up('lg')]: {
            maxHeight: pxToRem(229),
          },
        },
        '& .img-to-animate': {
          [theme.breakpoints.up('lg')]: {
            maxHeight: pxToRem(274.8), // added 20% to max height for paralax animation.
          },
          '& img': {
            [theme.breakpoints.up('lg')]: {
              maxHeight: 'none',
            },
          },
        },
      },
    },

    //Type 3 Carousel Specific Overwrites
    '&.Type3': {
      backgroundColor: props =>
        get(props, 'params.backgroundColor', theme.mixins.White()),
      paddingBottom: '0',
      [theme.breakpoints.up('lg')]: {
        marginBottom: theme.spacing(9),
      },
      '&.no-cta': {
        '& h2': {
          marginBottom: theme.spacing(3),
          [theme.breakpoints.up('lg')]: {
            marginBottom: theme.spacing(5),
          },
        },
        '& .slick-prev, & .slick-next': {
          top: theme.spacing(-15),
        },
      },
      '& .slick-slider': {
        ...theme.mixins.paddingLR(0, `calc(5.5% - ${theme.spacing(4)})`),
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.paddingLR('5.5%', '5.5%'),
        },
      },
      '& .slick-slide': {
        [theme.breakpoints.up('lg')]: {
          '&:not(.slide-active) .listing-card': {
            boxShadow: 'none',
          },
        },
      },
      '& .slick-prev': {
        top: theme.spacing(-17),
        left: 'auto',
        transform: 'none',
        right: '12.5%',
      },
      '& .slick-next': {
        top: theme.spacing(-17),
        transform: 'none',
      },
      '& .slick-track': {
        '& .slick-slide': {
          '& h3': {
            ...theme.typography.cardTitle1, // as per long page design, changed card title styling to cardTitle1 from h5
          },
        },
      },
    },

    //Type 1 Carousel Specific Overwrites
    '&.Type1': {
      background: props =>
        get(
          props,
          'params.backgroundColor',
          'linear-gradient(180deg, rgba(255,255,255,1) 50%, rgba(227,233,243,1) 100%);'
        ),

      '& .slick-slider': {
        // ...theme.mixins.paddingLR(0, `calc(5.5% - ${theme.spacing(4)})`),
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.paddingLR('5.5%', pxToRem(44)),
        },
      },
      '& .slick-list': {
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.paddingRight(pxToRem(44)),
          ...theme.mixins.marginRight(pxToRem(-44)),
        },
      },
      '& .slick-slide': {
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.marginLeft(pxToRem(0)),
          ...theme.mixins.marginRight(pxToRem(48)),
        },
      },
      '& .common-card-tag': {
        backgroundColor: theme.mixins.White(),
      },
      '& .card-wrapper:hover': {
        '& .common-card-tag': {
          backgroundColor: theme.palette.background.default,
        },
      },
    },
  },
  description: {
    background: 'none',
    position: 'static',
    padding: '0',
    width: '100%',
    height: 'auto',
    [theme.breakpoints.up('lg')]: {
      position: 'absolute',
      width: '65.25%',
      top: '0',
      ...theme.mixins.right('0'),
      zIndex: '10',
      ...theme.mixins.paddingLeft(pxToRem(48)),
      ...theme.mixins.paddingRight(theme.spacing(27)),
      paddingBottom: theme.spacing(8),
      paddingTop: theme.spacing(1),
      background:
        'linear-gradient(-180deg, rgb(255, 255, 255) 0%, rgba(255, 255, 255, 0.89) 100%)',
    },
  },
  descriptionType3: {
    paddingBottom: '0',
    '& h2': {
      marginBottom: theme.spacing(3),
    },
    [theme.breakpoints.up('lg')]: {
      paddingBottom: theme.spacing(5),
      minHeight: pxToRem(220),
    },
  },
  collageType1: {
    backgroundColor: theme.mixins.Black(),
  },
  headingContent: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  headingBackground: {
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundImage: props => {
      return `url(${get(props, 'fields.mobileImage.value.src', '/')})`;
    },
    [theme.breakpoints.up('lg')]: {
      backgroundImage: props => {
        return `url(${get(props, 'fields.desktopImage.value.src', '/')})`;
      },
    },
  },
  collageItems: {
    position: 'relative',
    top: theme.spacing(-6),
    [theme.breakpoints.up('lg')]: {
      top: theme.spacing(-11),
    },
  },
  headingSection: {
    color: theme.mixins.White(),
    paddingBottom: theme.spacing(7),
    paddingTop: theme.spacing(6),
    '& span': {
      color: theme.mixins.White(),
    },
    '& .MuiButton-endIcon': {
      backgroundColor: `${theme.palette.primary.contrastText} !important`,
      '& svg': {
        color: theme.palette.primary.main,
      },
    },
    '& .segment-title:before': {
      backgroundColor: theme.mixins.White(),
    },
    [theme.breakpoints.up('lg')]: {
      paddingTop: theme.spacing(8),
      paddingBottom: theme.spacing(15),
      '& .MuiButton-root.btnWithBorder': {
        borderTopColor: theme.mixins.White(200),
      },
    },
  },
  collageType1Logo: {
    padding: theme.spacing(3),
    paddingTop: theme.spacing(6),
    textAlign: theme.mixins.textRight(),
    '& img': {
      maxWidth: '75%',
    },
    [theme.breakpoints.up('lg')]: {
      padding: theme.spacing(11),
      '& img': {
        maxWidth: '100%',
      },
    },
  },
  discoverMore: {
    transform: `translateY(${theme.spacing(-6)})`,
  },
  rtlClass: {
    '&  [dir="ltr"] .slick-prev::before,& [dir="ltr"] .slick-next::before': {
      content: "''",
    },
    '&.Type3 [dir="ltr"] .slick-prev': {
      [theme.breakpoints.up('lg')]: {
        top: theme.spacing(-17),
        ...theme.mixins.left('auto'),
        ...theme.mixins.right('5.5%'),
      },
    },
    '&.Type3 [dir="ltr"] .slick-next': {
      [theme.breakpoints.up('lg')]: {
        top: theme.spacing(-17),
        ...theme.mixins.right('12.5%'),
        ...theme.mixins.left('auto'),
      },
    },
    '&  [dir="ltr"] .slick-prev': {
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.right('5.5%'),
        ...theme.mixins.left('auto'),
        top: pxToRem(114),
      },
    },
    '&  [dir="ltr"] .slick-next': {
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.right('5.5%'),
        ...theme.mixins.left('auto'),
        top: pxToRem(42),
      },
    },
    '& .slick-slider': {
      [theme.breakpoints.down('md')]: {
        direction: 'rtl',
      },
    },
  },
}));

export default useStyles;
